import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { togleTodo } from "../../redux/reducer";

const style = {
  li: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: ".5rem 1rrem",
    border: "1px solid #ccc",
    borderRadius: "5px",
    marginBottom: ".5rem",
  },
  btn: {
    background: "red",
    borderRadius: "5px",
    color: "#fff",
    border: "none",
  },
};

export default function TodoItem({ element }) {
  const dispatch = useDispatch();
  const { done } = useSelector(({ reducer }) => {
    return {
        done: reducer.watchList.done,
    };
  });
  return (
    <li style={style.li}>
      <span>
        <input
          type="checkbox"
          style={{ margin: "0 1rem" }}
          onChange={() => dispatch(togleTodo(element.id))}
          checked={done}
        />
        {element.name}
      </span>
      <button style={style.btn}>&times;</button>
    </li>
  );
}
